<template>
  <el-row :gutter="24">
    <el-col :span="6">
      <el-table
        :data="tableData"
        style="width: 100%;margin-bottom: 20px;"
        row-key="dev_mac"
        border
        default-expand-all
        @cell-click="drawRow"
        :tree-props="{children: 'devices', hasChildren: 'hasChildren'}">
        <el-table-column
          prop="dev_name"
          label="设备名称"
          width="300px">
        </el-table-column>
      </el-table>
    </el-col>
    <el-col :span="18">
        <div id="myChart" :style="{width: '100%', height: '400px'}"></div>
        <div id="myBar" :style="{width: '100%', height: '300px'}"></div>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    name: 'Device',
    data () {
      return {
        company_id: '',
        tableData:[],
      }
    },
    mounted(){
      this.drawLine();
    },
    methods: {
      initData(){
        let th = this
        this.axios.get('/api/get_devices',{
          params:{
            company_id:this.company_id,
          }
        }).then(function(res){
          th.tableData=res.data
        }).catch(function (error) {
          console.log('come')
          console.log(error);
        });
      },
      drawLine(title, times){
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        myChart.setOption({
          title: { text: title },
          tooltip: {trigger: 'axis'},
          xAxis: {
            type: 'time',
            splitLine: {
              show: false
            }
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, '100%'],
            splitLine: {
              show: false
            }
          },
          visualMap: {
            top: 24,
            x: 'center',
            orient: 'horizontal',
            textStyle: {
              color: '#000'
            },
            pieces: [
              {min:41,max:106, label: '正常运行', color: '#B5C334'},
              {min:10.9,max:41,label: '故障状态', color: '#FF4444'},
              {min:0,max:10.9,label: '关闭状态', color: '#00FF44'},
            ],
            outOfRange: {
              color: '#999'
            }
          },
          series: [{
            name: '状态',
            type: 'line',
            showSymbol: false,
            hoverAnimation: false,
            data: times,
            itemStyle: {
              normal: {
                color: function(params){
                  if(params.data[1]===42||params.data[1]===74||params.data[1]===106){
                    return '#B5C334';
                  }else if(params.data[1]<11){
                    return '#00FF44';
                  }else {
                    return '#FF4444'
                  }
              }
            }
            }
          }]
        });
      },
      drawBar(title, data, labels){
        let myChart = this.$echarts.init(document.getElementById('myBar'))
        myChart.setOption({
          title: { text: title },
          tooltip: {trigger: 'axis'},
          xAxis: {
            type: 'category',
            data: labels
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: data,
            type: 'bar'
          }]
        });
      },
      drawRow(row, column, cell, event){
        if (typeof (row.online)==="undefined"){
          let th = this
          this.axios.get('/api/get_history',{
            params:{
              dev_mac:row.dev_mac,
            }
          }).then(function(res){
            console.log(res.data)
            th.drawLine(row.dev_name, res.data.data)
            th.drawBar(row.dev_name, res.data.times, res.data.labels)
          }).catch(function (error) {
            console.log('come')
            console.log(error);
          });
        }
      },
    },
    created() {
      this.company_name = sessionStorage.getItem('company_name')
      this.company_id = sessionStorage.getItem('company_id')
      this.initData()

    }

  }

</script>

<style scoped>

</style>



