<template>
  <div>
    <el-dialog title="添加管理员" :visible="dialogFormVisible"  width="30%" :show-close="false">
      <el-form :model="form" label-position="left" ref="updateForm">
        <el-form-item label="管理员名称" :label-width="formLabelWidth">
          <el-input v-model="form.user_name" autocomplete="off" ></el-input>
        </el-form-item>
        <el-form-item label="管理员电话" :label-width="formLabelWidth">
          <el-input v-model="form.user_tel" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="管理权限" :label-width="formLabelWidth">
          <el-select v-model="form.user_auth" placeholder="请选择权限">
            <el-option
              v-for="item in auth_options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateUser">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="确定删除吗？"
      :visible.sync="dialogVisible"
      width="30%"
      >
      <el-form :model="form" label-position="left" ref="deleteForm">
        <el-form-item label="请选择设备承接人" :label-width="formLabelWidth">
          <el-select v-model="change_id" >
            <el-option
              v-for="item in tableData"
              :key="item.id"
              :label="item.user_name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="deleteUser">确 定</el-button>
     </span>
    </el-dialog>
    <el-col>
      <el-row :gutter="24" style="background-color: #ffffff;margin: 0px; border: 1px solid #eee">
        <el-col :span="1" :offset="21">
          <el-button type="primary" icon="el-icon-circle-plus" style="margin: 5px" @click="addData">添加管理员</el-button>
        </el-col>
      </el-row>
      <el-table
        :data="tableData.filter(data => !search || data.user_tel.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column
          label="添加日期"
          prop="add_time">
        </el-table-column>
        <el-table-column
          label="管理者名字"
          prop="user_name">
        </el-table-column>
        <el-table-column
          label="管理者电话"
          prop="user_tel">
        </el-table-column>
        <el-table-column
          label="权限"
          prop="user_auth"
        >
        </el-table-column>
        <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder="输入电话搜索"/>
          </template>

          <template slot-scope="scope">
            <el-button
              size="mini"
              type="warning"
              @click="handleEdit(scope.$index, scope.row)">修改</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table></el-col>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        company_name:'',
        company_id:'',
        tableData: [],
        search: '',
        dialogFormVisible: false,
        formLabelWidth: '140px',
        dialogVisible:false,
        tobeDelete:'',
        form: {
          user_name: '',
          user_auth: 0,
          user_tel:''
        },
        org_form:{
          user_name: '',
          user_auth: 0,
          user_tel:''
        },
        auth_options:[
          {
            label:'仅可以管理自己的设备',
            value:0
          },
          {
            label:'可以管理所有的设备',
            value:1
          },{
            label:'仅可以查看所有的设备',
            value:2
          },
        ],
        change_id: '',
        delete_id:'',
        select_value:'',
      }
    },
    methods: {
      initData(){
        let th = this
        this.axios.get('/api/get_all_user',{
          params:{
            company_id:this.company_id,
          }
        }).then(function(res){
          console.log(res.data)
          th.tableData=res.data.data
        }).catch(function (error) {

          console.log(error);
        });
      },
      handleEdit(index, row) {

        this.dialogFormVisible=true
        this.form=row
      },
      handleDelete(index, row) {
        this.dialogVisible=true
        this.tobeDelete=row.user_tel
        this.delete_id = row.id
      },
      change(index, row){

      },
      addData(){
        this.dialogFormVisible=true
        this.form = this.org_form
      },
      updateUser(){
        this.dialogFormVisible=false
        let th = this
        this.$refs.updateForm.validate(valid => {
          if (valid) {
            if (typeof (this.form.id)==="undefined"){
              console.log('add')
              this.axios.get('/api/add_user',{
                params:{
                  user_name:this.form.user_name,
                  user_auth:this.form.user_auth,
                  user_tel:this.form.user_tel
                }
              }).then(function(res){
                if (res.data.status===200){
                  th.initData()
                }

              }).catch(function (error) {
                console.log(error);
              });
            }else {
              console.log('update')
              this.axios.get('/api/update_user',{
                params:{
                  user_name:this.form.user_name,
                  user_auth:this.form.user_auth,
                  user_tel:this.form.user_tel,
                  user_id:this.form.id
                }
              }).then(function(res){
                if (res.data.status===200){
                  th.initData()
                }
              }).catch(function (error) {
                console.log(error);
              });
            }
          }
        })
      },
      deleteUser(){
        console.log('ss')
        this.dialogVisible=false
        let th = this
        if(th.change_id===th.delete_id || th.change_id===''){
          th.$message.error('不能把设备转交给要删除的人！');
        }else {
          this.axios.get('/api/delete_user',{
            params:{
              delete_id:this.delete_id,
              change_id:this.change_id
            }
          }).then(function(res){
            if (res.data.status===200){
              th.change_id=''
              th.initData()
            }
          }).catch(function (error) {
            console.log(error);
          });
        }
      }
    },
    created() {
      // console.log(this.$route.params)
      //this.company_name = this.$route.params['company_name']
      //this.company_id = this.$route.params['company_id']
      this.company_name = sessionStorage.getItem('company_name')
      this.company_id = sessionStorage.getItem('company_id')
      this.initData()

    }


  }
</script>



